<template lang="html">
  <div class="puzzle-container" :style="puzzleStyle" ref="holder">
    <Square
      :size="squareSize"
      v-for="(sq, idx) in squares"
      :key="idx"
      :id="sq.id"
      :data="sq"
      :colors="colors"
      :activeColor='activeColor'
      @click="squareClick(sq)"
      @hover="squareHover(sq)"
      />
  </div>
</template>

<script>
import Square from './Square.vue';

export default {
  props: ['width', 'height', 'selectedShape', 'colors', 'activeColor'],
  components: {
    Square,
  },
  computed: {
    puzzleStyle() {
      const grid = `grid-template: repeat(${this.width}, ${this.squareSize}px) / repeat(${this.height}, ${this.squareSize}px);`;
      return [grid].join('');
    },
  },
  data() {
    return {
      squares: null,
      squareSize: 0,
      screenWidth: 0,
      screenHeight: 0,
      currentlyHovered: [],
      hoverShapes: {
        '+': [1, -1, (this.width) * -1, (this.width)],
        'x': [(this.width * -1) + 1, (this.width * -1) - 1, this.width + 1, this.width - 1],
      },
      lastHovered: null,
    };
  },
  watch: {
    selectedShape() {
      this.squareHover(this.lastHovered);
    },
  },
  mounted() {
    //  Just normal setup stuff, nothing to see here
    this.screenWidth = this.$refs.holder.clientWidth;
    this.screenHeight = this.$refs.holder.clientHeight;
    this.squareSize = this.width < this.height
      ? Math.floor((this.screenWidth) / (this.width))
      : Math.floor((this.screenHeight) / (this.height));
    const maxId = this.width * this.height;
    this.squares = [];
    for (let i=0; i < maxId; i++) {
      this.squares.push({
        id: i,
        color: this.colors[0],
        hovered: false,
      });
    }
  },
  methods: {
    squareHover(sq) {
      //  Move that hover mask around, show a visual of what squares will get changed if you click
      this.lastHovered = sq;
      this.currentlyHovered.forEach((item) => {
        item.hovered = false;
      });
      this.currentlyHovered = [];
      this.hoverShapes[this.selectedShape].forEach((offset) => {
        const newIdx = sq.id + offset;
        const outOfBounds = newIdx < 0 || newIdx >= this.squares.length;
        const mod1 = (newIdx % this.width);
        const mod2 = (sq.id % this.width);
        const edgeOverlap = (mod1 + mod2  === this.width - 1) && Math.min(mod1, mod2) === 0;
        if (!(outOfBounds || edgeOverlap))
          this.currentlyHovered.push(this.squares[newIdx]);
      });
      this.currentlyHovered.push(sq);
      this.currentlyHovered.forEach((item) => {
        item.hovered = true;
      })
    },
    squareClick() {
      if (this.activeColor !== null) {
        this.currentlyHovered.forEach((item) => {
          item.color = this.activeColor;
        })
      }
    }
  }
}
</script>

<style lang="css" scoped>
.puzzle-container {
  width: 100%;
  height: 100%;
  display: grid;
  grid-gap: 0px;
}
</style>
