<template lang="html">
  <div class="puzzle-square"
  :style="style"
  @mouseover="$emit('hover')"
  >
    <div class="hover-region" v-if="data.hovered" :style="`background-color: ${activeColor ? activeColor : 'rgba(255, 255, 255, 0.3)'}`">

    </div>
  </div>
</template>

<script>
export default {
  props: ['size', 'data', 'colors', 'activeColor'],
  computed: {
    style() {
      const output = [];
      const sizeStyle = `height: ${this.size}px; width: ${this.size}px;`;
      output.push(sizeStyle);
      const bg = `background-color: ${this.data.color};`
      output.push(bg);
      return output.join('');
    },
  },
}
</script>

<style lang="css" scoped>
.puzzle-square {
  border: 1px solid #aaa;
  display: inline-block;
}
.hover-region {
  width: 100%;
  height: 100%;
}
</style>
