<template lang="html">
  <div class="container">
    <div class="color-select-container"
      :style="`width: ${parseInt(100 / Object.keys(colorMap).length)}vh`"
    >
      <div class="color-option"
      v-for="(item,idx) in colorMap"
      :key="idx"
      >
        <div class="color-preview"
        :style="`background-color: ${item}`"
        @click="selectColor(item)"
        />
      </div>
    </div>
    <Puzzle
    :width="width"
    :height="height"
    :selectedShape="selectedShape"
    :colors="colorMap"
    :activeColor="activeColor"
    class="puzzle"
    />

  </div>
</template>

<script>
import Puzzle from './Puzzle.vue';

export default {
  components: {
    Puzzle,
  },
  computed: {
    shapePlus() {
      return [1, -1, this.width * -1, this.width];
    },
    shapeX() {
      return [(this.width * -1) + 1, (this.width * -1) - 1, this.width + 1, this.width - 1];
    }
  },
  data() {
    return {
      width: 10,
      height: 10,
      keyShapeSelection: {
        '1': '+',
        '2': 'x',
      },
      selectedShape: '+',
      colorMap: {
        0: '#EEBAB2',
        1: '#CCD4BF',
        2: '#E7CBA9',
      },
      activeColor: null,
    };
  },
  methods: {
    handleKeyPress(event) {
      if (event.key in this.keyShapeSelection) {
        this.selectedShape = this.keyShapeSelection[event.key];
      }
    },
    selectColor(item) {
      this.activeColor=item;
    },
  },
  created() {
    window.addEventListener('keypress', this.handleKeyPress);
  },
  unmounted() {
    window.removeEventListener('keypress', this.handleKeyPress);
  },
}
</script>

<style lang="css" scoped>
.container {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.color-select-container {
  height: 100%;
  display: flex;
  flex-direction: column;
}

.puzzle {
  width: auto;
  height: 100vh;
}

.color-option {
  border: 1px dashed white;
  flex-grow: 1;
  display: flex;
  padding: 2%;
  margin: 5%;
}

.color-preview {
  flex-grow: 1;
}

</style>
